
import { Options, Vue } from 'vue-class-component';

@Options({
    props: {
        image: String
    },
    components: {
        
    },
})
export default class MaterialCard extends Vue {

    image?: string;

    get style(): string {
        if (!this.image) return '';
        return `background-image: url("${this.image}")`;
    }
}
